@import "../constants/colors.scss";

.search {
  display: flex;
  flex-direction: column;
  align-items: center;

  &__text-wrapper {
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 38px 29px 32px 29px;
  }

  &__title {
    font-size: 21px;
    font-weight: 700;
    line-height: 26px;
    padding-bottom: 20px;
  }

  &__text {
    font-size: 14px;
    font-weight: 400;
    line-height: 21px;
    text-transform: uppercase;
    text-align: center;
  }

  &__button {
    background-color: $button-blue;
    color: rgb(255, 255, 255);
    font-size: 21px;
    font-weight: 700;
    line-height: 26px;
    text-transform: uppercase;
    padding: 16px 49px 16px 49px;
    min-width: 284px;
    min-height: 58px;
    margin-bottom: 49px;
  }
}
